
@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@200;300;400;500;600;700;800&display=swap');


* {
  box-sizing: border-box;

  padding: 0;

  margin: 0;

  
}

.main_div {
  width: 100vw;

  height: 100vh;

  background:linear-gradient(to top right, #33ccff 0%, #ff99cc 100%);

  display: flex;

  flex-direction: row;

  justify-content: center;

  align-items: center;

  text-align: center;
}

.center_div {
  max-width: 70%;

  max-height: 70%;

  background-color: #f4f4f4;

  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);

  border-radius: 15px;

  /* add scrollbar */
  overflow: auto;
}

h1 {
  color: rgb(233, 7, 101);
  background-color: transparent;
  font-size: 50px;
  margin-bottom: 30px;
  min-width: 64px;
  box-sizing: border-box;
  font-family: "Mukta", sans-serif;
  font-weight: 500;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}

input {
  text-align: center;
  height: 30px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-right: 20px;
  margin-bottom: 20px;
}

.newBtn {
  width: 50px;
  height: 55px;
  background-color: rgb(233, 7, 101) !important;
  color: white !important;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
}

.newBtn:hover {
  background: rgb(42, 211, 9) !important;
}

/* //the add symbol inside the button*/

.MuiSvgIcon-root {
  width: 2rem !important;
  height: 2rem !important;
}

ol {
  margin-top: 30px;
}

ol li {
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  list-style: none;
 
}

.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .deleteIcon {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  padding: 5px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

.todo_style .deleteIcon:hover {
  background-color: transparent;
  color: crimson;
  box-shadow: 5px 5px 15px -5px rgba(255, 0, 0, 0.5);
}



@media (max-width: 768px) {
  .main_div {
    flex-direction: column;
    height: 100vh;
  }

  .center_div {
    width: 80%;
    height: auto;
  }

  h1 {
    font-size: 30px;
  }

  input {
    width: 80%;
  }

  .todo_style .deleteIcon {
    margin: 0 15px 0 15px;
  }

}